import React, {useState, useEffect } from 'react';


function TodoForm({addTodo}) {

    const [value, setValue] = useState('');
    
    const handleSubmit = e => {
        e.preventDefault();
        addTodo(value);        
        setValue("");
    }


    return (
        <form className="form" onSubmit={handleSubmit}>
        
            <input 
                required
                type="text"
                value={value}    
                onChange={e => setValue(e.target.value)}
            />
            
            <button title="Add task">+</button>
        </form>
    )
}

function Todo({ todo, index, completeTodo, removeTodo, text }) {
    return (
        <>
        <div className="todo-item-box">
            <input type="checkbox" onChange={() => completeTodo(todo.text)} checked={todo.isCompleted}/>

            <div className="todo" 
                style={{ 
                textDecoration: todo.isCompleted ? "line-through" : "none",
                color: todo.isCompleted ? "green" : ""
                }}>

                {todo.text}

            </div>

            <button onClick={() => removeTodo(todo.text)} title="Remove task from list">
                X
            </button>
        </div>
        </>
    )
}

function Component() {    

    useEffect( () => {
        const todos = localStorage.getItem("todos") 
        setTodos(todos ? JSON.parse(todos) : []) 
    }, [])

    const removeTodo = text => {

        const newTodos = todos.filter(el => el.text !== text) ;
        setTodos(newTodos);

        localStorage.setItem("todos", JSON.stringify(newTodos));
    }

    const completeTodo = text => {

        const newTodos = todos.map(el => {
            if(el.text === text) {
                el.isCompleted = !el.isCompleted
            }
            return el
        })
        setTodos(newTodos);

        localStorage.setItem("todos", JSON.stringify(newTodos));
    }

    const addTodo = text => {
        const newTodos = [ ...todos, { text, isCompleted: false }];
        setTodos(newTodos);

        localStorage.setItem("todos", JSON.stringify(newTodos));
    }

    const [todos, setTodos] = useState( [] );

    return(
        <div className="app">

            <h1>You have { todos.length > 0 ? todos.length : "no" } Task</h1>

            <div className="todo-list">

                <TodoForm addTodo={addTodo} />

                <ul>
                    {todos.map((todo, index) => (

                        <li key={index} className="todo-item" style={{ 
                            border: todo.isCompleted ? "1px solid green" : ""}} > 
                            <Todo
                                index={index}
                                todo={todo}
                                completeTodo={completeTodo}
                                removeTodo={removeTodo}
                            />
                        </li>

                    ))}
                </ul>

            </div>
        </div>
    )
}

export default Component;